.btn{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  border-radius: 6px;
  transition: .4s;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  &.--enter{
    background-color: #0005A5;
    color: #fff;
    width: 254px;
  }
  &.--blue{
    min-height: 60px;
    height: auto;
    padding: 11px 32px;
    background-color: #0005A5;
    color: #fff;
    line-height: 30px;
    font-weight: 600;
    justify-content: center;
    text-align: center;
    flex-wrap: wrap;
  }
  &.--white{
    border: 2px solid #FFFFFF;
    color: #fff;
  }
}

.btn-center{
  display: flex;
  justify-content: center;
}

@media(max-width: 768px){
  .btn{
    font-size: 18px;
    width: 100%;
  }
  .btn span{
    display: none;
  }
}
@media(max-width: 460px){
  .btn{
    height: 56px;
  }
}