/* Main Colors */
$color-base : #1B1B1B;
$color-bg : #fff;
$color-text : $color-base;
$color-overlay : $color-base;
$opacity-overlay: .4;
$link-color : $color-base;



/* Fonts */
$font-family : 'Roboto', sans-serif;
$mont: "Montserrat", sans-serif;
$font-title: $font-family;
$font-size : 16px;
$font-weight : normal;
$letter-spacing : normal;
$line-height : 1.3;
$placeholder: #BDBDBD;



/*container*/
$container-padding-left: 20px;
$container-padding-right: 20px;
$container-width: 1470px + $container-padding-left + $container-padding-right;


/*styles*/

$brs: 10px;