.main   {
  padding: 334px 0 550px;
  background: url(../img/main.svg) no-repeat left bottom / 1950px auto;
  min-height: 1234px;
  p{
    font-size: 24px;
    line-height: 28px;
    color: #fff;
    max-width: 840px;

  }
}

@media(min-width: 1950px){
  .main   {
    min-height: 1334px;
    background: url(../img/main.svg) no-repeat right bottom / 100% auto;
  }
}

.contest{
  margin-top: -50px;
}
.contest__list {
  display: flex;
  flex-wrap: wrap;
  margin-right: -90px;
}

.contest__item {
  width: calc(25% - 90px);
  margin-right: 90px;
}

.contest__pic{
  display: flex;
  justify-content: center;
  margin-bottom: 45px;
}

.contest__text{
  font-size: 26px;
  line-height: 30px;
  text-align: center;
}

.organization{
  padding-top: 210px;
  padding-bottom: 80px;
  background: url(../img/organization-bg.svg) no-repeat center bottom  / 2188px auto;
}

.organization__list {
  display: flex;
  justify-content: space-between;
  margin-bottom: 260px;
}

.organization__item{
  width: calc(33.33% - 30px);
  background-color: #fff;
  height: 213px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 20px;
  padding-left: 40px;
}

.organization__pic{
  margin-right: 40px;
  width: 120px;
}

.organization__text{
  font-size: 20px;
  line-height: 23px;
  width: calc(100% - 160px);
}
