.header{
  position: absolute;
  left: 0;
  top: 0;
  padding-top: 46px;
  width: 100%;
}

.socials{
  display: flex;
}

.socials__link{
  margin-right: 16px;
  &:last-child{
    margin-right: 0px;
  }
}

.nav {
  display: flex;
}

.nav__link{
  margin-right: 46px;
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  text-transform: uppercase;
  .footer &{
    color: #fff;
  }
  svg{
    margin-right: 10px;
  }
  &:last-child{
    margin-right: 0px;
  }
}

