.body {
  background: $color-bg;
  color: $color-text;
  font-family: $font-family;
  font-size: $font-size;
  letter-spacing: $letter-spacing;
  line-height: $line-height;
  scroll-behavior: smooth;

  &.--frozen {
      overflow: hidden;
  }
}

.container {
  max-width: $container-width;
  padding-left: $container-padding-left; 
  padding-right: $container-padding-right;
  margin: 0 auto;
  position: relative;
  z-index: 50;
  width: 100%;
}

.hidden {
  display: none;
}

.content{
  position: relative;
  z-index: 10;
}

a {
  cursor: pointer;
  color: $link-color;
  text-decoration: none;
}


strong {
  font-weight: bold;
}

::placeholder {
  color: $placeholder;
  transition: .4s;
}

:focus::placeholder {
  opacity: 0;
}

::-webkit-scrollbar {
  width: 0px; 
  background: transparent;
}

img{
  max-width: 100%;
  display: block;
}

form{
  display: block;
  width: 100%;
}

.link{
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  text-decoration: underline;
}



h1, h2, h3, h4, h5, h6{    
  font-weight: normal;
}


h1{
  font-family: $mont;
  font-style: normal;
  font-weight: bold;
  color: #fff;
  max-width: 840px;
  font-size: 68px;
  line-height: 64px;
  margin-bottom: 53px;
}

h2{
  font-family: $mont;
  font-weight: bold;
  font-size: 56px;
  line-height: 64px;
  margin-bottom: 65px;
  text-align: center;
}